a {
  color: #3c6b37;
}

table {
	display: block;
  overflow-x: auto;
  white-space: nowrap;
  border-collapse: collapse;
  width: 100%;
	margin-top: 35px;
	margin-bottom: 40px;
}

td, th {
  border-bottom: 1px solid #acacb4;
  text-align: left;
  padding: 8px;
}

td:nth-child(even) {
	background-color: #dedede;
}

.progress-report-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 20px;
}

.progress-report-main {
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	align-items: center;
	text-align: left;
}

.progress-report-title {
	width: 100% !important;
}

.progress-report-subtitle {
	width: 100% !important;
}
