.about-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 120px;
}

.about-main {
	display: flex;
}

.about-left-side {
	display: flex;
	flex-direction: column;
	flex-basis: auto;
}

.about-title {
	width: 100% !important;
}

.about-subtitle {
	width: 80% !important;
}

.about-cards {
	padding-top: 85px;
	margin-right: 80px;
}

.about-right-side {
	display: flex;
	flex-direction: column;
	flex-basis: auto;
}

.about-image-container {
	width: 370px;
}

.about-image-wrapper {
	display: inline-block;
	overflow: hidden;
	width: 100%;
	border-radius: 10%;
	transform: rotate(3deg);
}

.about-image-wrapper img {
	display: block;
	width: 100%;
}

.about-socials {
	padding-top: 50px;
	padding-bottom: 50px;
}

.about-socials-mobile {
	display: none;
	margin-bottom: 30px;
}

@media (max-width: 1024px) {
	.about-main {
		flex-direction: column;
	}

	.about-left-side {
		flex-basis: 100%;
		order: 1;
	}

	.about-subtitle {
		padding-top: 20px !important;
	}

	.about-cards {
		margin-right: 0px;
	}

	.about-right-side {
		flex-basis: 100%;
		order: 2;
	}

	.about-image-container {
		padding-bottom: 40px;
	}

	.about-socials {
		display: none;
	}

	.about-socials-mobile {
		padding-top: 30px;
		display: flex;
	}
}

@media (max-width: 600px) {
	.about-container {
		padding-top: 60px;
	}

	.about-image-container {
		width: 80%;
	}
}
