.hero-image {
	background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(../../assets/images/hero_image.jpg);
	background-position: center;
  background-repeat: no-repeat;
	background-size: cover;
}

.home-container {
	display: flex;
	padding-top: 220px;
	padding-bottom: 200px;
}

.home-main {
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	align-items: center;
	text-align: center;
}

.home-title {
	width: 85% !important;
	color: white;
}

.home-subtitle {
	width: 85% !important;
	color: white;
	margin-top: 0;
	margin-bottom: 25px;
}

.home-socials {
	display: flex;
	justify-content: left;
	align-items: center;
	padding-top: 30px;
}

.home-social-icon {
	padding-right: 20px;
	font-size: 25px;
	transition: color 0.2s ease-in-out;
	color: white;
}

.home-social-icon:hover {
	color: var(--secondary-color);
}

@media (max-width: 1024px) {
	.home-main {
		flex-direction: column;
	}

	.home-title {
		width: 100% !important;
		margin-top: 20px;
		order: 2;
	}

	.home-subtitle {
		width: 100% !important;
		margin-top: 10px;
		order: 3;
	}
}
