.codes-body {
	margin-bottom: 10px;
}

.code {
	display: flex;
	padding-bottom: 40px;
	width: 400px;
}

.code-icon {
	height: 30px;
	width: 30px;
	border-radius: 10%;
	outline: 6px solid white;
	box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.25);
}

.code-name {
	font-size: 15px;
	font-weight: 700;
	padding-left: 20px;
	margin-top: -3px;
	color: var(--secondary-color);
}

.code-development-type {
	position: absolute;
	font-size: 12px;
	color: var(--secondary-color);
	padding-top: 22px;
	padding-left: 50px;
}

.code-type {
	position: absolute;
	font-size: 12px;
	color: var(--tertiary-color);
	padding-top: 42px;
	padding-left: 50px;
}
